import React from 'react';
import withColors from '../../withColors';
import withFont from '../../withFont';
import withWeight from '../../withWeight';

// const typeProps = "fontSize style textAlign lineHeight color dark primary secondary error noColor light bold weight semi".split(" ");
const typeProps = 'style className'.split(' ');

function sanitizeTypeProps(props) {
  return typeProps.reduce((memo, p) => {
    memo[p] = props[p];
    return memo;
  }, {});
}

const TypeFace = withColors(
  withWeight(
    withFont(({ component = 'h2', children, ...props }) =>
      React.createElement(component, sanitizeTypeProps(props), children)
    )
  )
);

function Type135(props) {
  return <TypeFace {...props} component="p" fontSize={13.5} />;
}

function Type135Light(props) {
  return <Type135 {...props} weight={300} />;
}

function Type135Semi(props) {
  return <Type135 {...props} weight={600} />;
}

function Type135Dark(props) {
  return <Type135 {...props} dark />;
}

