import React from 'react';
import PropTypes from 'prop-types';

export default function withFont(WrappedComponent) {
    return class extends React.Component { // eslint-disable-line
    static displayName = `withFont(${WrappedComponent.displayName ||
      WrappedComponent.name ||
      'Component'})`;

    static propTypes = {
      fontSize: PropTypes.number,
      style: PropTypes.object,
      textAlign: PropTypes.string
    };

    static defaultProps = {
      fontSize: 25,
      style: {},
      textAlign: 'left'
    };

    render() {
      const { fontSize = 25, style = {}, textAlign = 'left', ...props } = this.props;
      return (
        <WrappedComponent
          style={{
            ...style,
            fontSize,
            lineHeight: `${fontSize * 1.75}px`,
            textAlign
          }}
          {...props}
        />
      );
    }
  };
}
