class DesignSystem {
  fontFamily = 'Open-Sans; sans-serif';

  fontWeights = {
    light: 100,
    regular: 500,
    bold: 800
  };

  fontSizes = [111, 77, 45, 37, 30, 25, 20, 24, 18, 15, 14, 12];

  fontSizeNames = [
    'Uber',
    'Hero',
    'H1 Heading',
    'H2 Heading',
    'H3 Heading',
    'H4 Heading',
    'H5 Heading',
    'Lead',
    'Body',
    'Small',
    'Caption',
    'Tiny'
  ];

  colors = {
    primary: {
      '11': '#1D8A8D',
      '1': '#219A9D',
      '03': '#25ACAF',
      '01': '#92D5D7'
    },
    secondary: {
      '1': '#004E64',
      '09': '#195F73',
      '07': '#4C8392',
      '04': '#99B8C1',
      '03': '#D6E2E6',
      '01': '#E5EDEF'
    },
    tertiary: {
      '1': '#FE7B73'
    },
    grey: {
      '1': '#54545B',
      '09': '#62626A',
      '08': '#8C8C98',
      '07': '#9D9EAB',
      '06': '#AFB0BE',
      '05': '#CFCFD8',
      '04': '#EFEFF2'
    },
    error: {
      '1': '#ED5242'
    },
    moderate1: {
      '1': '#FAA916',
      '04': '#FDDCA1'
    },
    moderate2: {
      '1': '#FE8F42',
      '05': '#FEC7A0'
    },
    white: {
      '1': '#ffffff'
    },
    black: {
      '1': '#000000'
    }
  };

  borderRadius = 8;

  getColorList() {
    return Object.keys(this.colors).reduce(
      (memo, name) =>
        memo.concat(
          Object.keys(this.colors[name]).map(variant => ({
            name: `${name} ${variant}`,
            color: this.colors[name][variant],
            key: `${name} ${variant}`
          }))
        ),
      []
    );
  }

  getColor(str) {
    if (str.indexOf('#') > -1) {
      return str;
    }
    const parts = str.split(' ');
    let color = this.colors[parts[0]];
    if (!color) color = this.colors.primary;
    let shade = color[parts[1]];
    if (!shade) {
      shade = color['1'];
    }
    return shade;
  }

  getContrastColor(str) {
    if (str.length === 3 || str.length === 4) {
      if (str === '#fff') {
        return 'black';
      }
      return 'white';
    }
    const s = str.replace('#', '');
    const sum = ['r', 'g', 'b'].reduce((memo,a,i) => memo + parseInt(s.slice(i*2, (i+1)*2), 16),0);
    return sum/3 > 200 ? 'black' : 'white';
  }
}

export default DesignSystem;
