import FontFaceObserver from 'fontfaceobserver';
import React from 'react';
import styles from './styles/main.css'; // eslint-disable-line

export default class OpenSansFont extends React.Component {
  state = { classes: '' };

  componentWillMount() {
    const openSansObserver = new FontFaceObserver('Open Sans', {});

    // When Open Sans is loaded, add a font-family using Open Sans to the body
    openSansObserver
      .load()
      .then(() => {
        this.setState({ classes: 'fontLoaded' });
      })
      .catch(() => {
        this.setState({ classes: 'fontLoaded' });
      });
  }

  render() {
    const { classes } = this.state;
    const { className = '', style = {}, children } = this.props;
    return <div className={`${classes} ${className}`} style={style}>{children}</div>;
  }
}
