import React from 'react';
import PropTypes from 'prop-types';
import DesignSystem from './design-system';
const design = new DesignSystem();

export default function withColors(WrappedComponent) {
    return class extends React.Component { // eslint-disable-line
    static displayName = `withColors(${WrappedComponent.displayName ||
      WrappedComponent.name ||
      'Component'})`;

    static propTypes = {
      color: PropTypes.string,
      primary: PropTypes.bool,
      secondary: PropTypes.bool,
      error: PropTypes.bool,
      style: PropTypes.object,
      dark: PropTypes.bool,
      noColor: PropTypes.bool
    };

    static defaultProps = {
      color: 'primary a1',
      style: {},
      dark: false,
      primary: false,
      secondary: false,
      error: false,
      noColor: false
    };

    render() {
      const { color, style, dark, primary, secondary, error, noColor, ...props } = this.props;
      let nColor = design.getColor(color);
      if (dark) {
        nColor = design.getColor("primary 11");
      }
      if (primary) {
        nColor = design.getColor("promary");
      }
      if (secondary) {
        nColor = design.getColor("secondary");
      }
      if (error) {
        nColor = design.getColor("error");
      }
      const nStyle = style;
      if (!noColor) {
        nStyle.color = nColor;
      } else {
        nStyle.color = null;
      }
      return <WrappedComponent style={nStyle} {...props} />;
    }
  };
}
