import React from 'react';
import PropTypes from 'prop-types';

export default function withWeight(WrappedComponent) {
    return class extends React.Component { // eslint-disable-line
    static displayName = `withWeight(${WrappedComponent.displayName ||
      WrappedComponent.name ||
      'Component'})`;

    static propTypes = {
      light: PropTypes.bool,
      bold: PropTypes.bool,
      semi: PropTypes.bool,
      style: PropTypes.object,
      weight: PropTypes.number
    };

    static defaultProps = {
      light: false,
      bold: false,
      semi: false,
      style: {},
      weight: 400
    };

    render() {
      const { light, bold, weight, semi, style, ...props } = this.props;
      let nWeight = weight;
      if (light) {
        nWeight = 300;
      }
      if (semi) {
        nWeight = 600;
      }
      if (bold) {
        nWeight = 700;
      }
      return <WrappedComponent style={{ ...style, fontWeight: nWeight }} {...props} />;
    }
  };
}
